  .NavBar__Item:before{
    content: "";
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 45px;
  left: 0;
  background-color: rgb(255, 255, 255);
  visibility: hidden;
  transition: all 0.4s ease-in-out;
  }


  .NavBar__Item:hover:before {
    visibility: visible;
    width: 100%;
  }

  .NavBar__Item:hover {
    transition: all 0.4s ease-in-out;
  }
