  .account-container input {
    height: 4vh;
    width: 22vw;
    font-family: 'Jost', sans-serif;
    border: 1px solid lightgrey;
    color: #3b3b3b;
    border-radius: 30px;
    background-color: rgba(255,255,255,0.85);
    font-size: 20px;
    padding: 20px;
    line-height: 2;
    transition: 300ms ease-in-out, color 300ms ease-in-out;
    margin-bottom: 15px;
    font-weight: 300;
    }
  
    input:hover, input:focus, input:focus-within, input:active {
      transition: 300ms ease-in-out, color 300ms ease-in-out;
      box-shadow: inset 0 14px 14px 0 rgba(0, 0, 0, 0.25), inset 0 10px 5px 0 rgba(0, 0, 0, 0.22);
      outline: none;
  }
  
  textarea:hover, textarea:focus, textarea:focus-within, textarea:active {
    transition: 100ms ease-in-out;
